const CareSearchPage = () => {
  const navigate = useNavigate();
  const [currentView, setCurrentView] = useState("view1");

  // parameter for view1
  const [clothes, setClothes] = useState("");
  const [garment1, setGarment1] = useState("");
  const [garment2, setGarment2] = useState("");
  const [garment3, setGarment3] = useState("");
  const [customInput1, setCustomInput1] = useState("");
  const [customInput2, setCustomInput2] = useState("");
  const [customInput3, setCustomInput3] = useState("");

  // parameter for view2
  const [inputText, setInputText] = useState("");

  const [isLoading, setIsLoading] = useState(false);
  const [retry, setRetry] = useState(false);

  const [error, setError] = useState("");

  const handleInputChange1 = (e) => {
    setCustomInput1(e.target.value);
  };

  const handleInputChange2 = (e) => {
    setCustomInput2(e.target.value);
  };

  const handleInputChange3 = (e) => {
    setCustomInput3(e.target.value);
  };

  const handleKeyPress = (e) => {
    if (e.key === "Enter") {
      //      
    }
  };

  const validateInputs = () => {
    const sum = [customInput1, customInput2, customInput3]
      .map(Number)
      .reduce((acc, curr) => acc + (isNaN(curr) ? 0 : curr), 0);

    if (sum !== 100) {
      setError("   100% !");
      return false;
    }

    setError("");
    return true;
  };

  // api call function for view 1
  async function handleSearchButtonClick1() {
    if (!validateInputs()) {
      alert("   100% ");
      return;
    }

    const input = [
      clothes,
      garment1,
      customInput1,
      garment2,
      customInput2,
      garment3,
      customInput3,
    ];

    if (null in input) {
      alert("Please fill in the form");
      return;
    }

    setIsLoading(true);

    try {
      const result = await CareLabelSearchAPI(input);
      if (result[1].length > 0) {
        navigate("/care-result", {
          state: { input: input, result: result },
        });
      } else {
        setIsLoading(false);
        setRetry(true);
      }
    } catch (error) {
      console.error("Error:", error);
      setIsLoading(false);
      setRetry(true);
    } finally {
      setIsLoading(false);
    }
  }

  // api call function for view 2
  async function handleSearchButtonClick2() {
    if (!inputText) {
      alert("  .");
      return;
    }
    setIsLoading(true);

    try {
      const result = await CareSearchAPI(inputText);
      navigate("/care-result2", {
        state: { result: result },
      });
      setIsLoading(false);
    } catch (error) {
      console.error("Error:", error);
      setIsLoading(false);
      setRetry(true);
    } finally {
      setIsLoading(false);
    }
  }

  return (
    <div className="h-auto">
      {currentView === "view1" ? (
        <View1
          clothes={clothes}
          garment1={garment1}
          garment2={garment2}
          garment3={garment3}
          setClothes={(x) => {
            setClothes(x);
          }}
          setGarment1={(x) => {
            setGarment1(x);
          }}
          setGarment2={(x) => {
            setGarment2(x);
          }}
          setGarment3={(x) => {
            setGarment3(x);
          }}
          customInput1={customInput1}
          customInput2={customInput2}
          customInput3={customInput3}
          handleInputChange1={handleInputChange1}
          handleInputChange2={handleInputChange2}
          handleInputChange3={handleInputChange3}
          handleKeyPress={handleKeyPress}
          setCurrentView={setCurrentView}
          handleSearchButtonClick={handleSearchButtonClick1}
          error={error}
        />
      ) : (
        <View2
          setCurrentView={setCurrentView}
          handleSearchButtonClick={handleSearchButtonClick2}
          setInputText={(x) => {
            setInputText(x);
          }}
        />
      )}
      <Loading isLoading={isLoading} isSig={false} />
      <RecogFail
        retry={retry}
        setRetry={(x) => {
          setRetry(x);
        }}
        isSig={false}
      />
    </div>
  );
};

export default CareSearchPage;
