import React from "react";
import { Link } from "react-router-dom";
import logo from "../../assets/images/title.png";
import likelion_logo from "../../assets/images/likelion.png";

const Footer = () => {
  return (
    <footer className="flex w-[1440px] h-[128px] items-center justify-between px-[45px] py-[30px] relative">
      <span className="inline-flex items-center gap-5 relative flex-[0_0_auto]">
        <a href="https://snulion-web.vercel.app/">
          <img
            className="relative w-16 h-[68px] object-cover -z-20"
            alt="likelion"
            src={likelion_logo}
          />
        </a>
        <Link to="/">
          <img
            className="relative w-44 h-[46px] object-cover"
            alt="carewise"
            src={logo}
            onClick={() => {
              window.scrollTo(0, 0);
            }}
          />
        </Link>
      </span>
      <span className="flex flex-col w-[272px] items-start gap-2.5 relative">
        <p className="relative inter-regular w-[272px] mt-[-1.00px] text-[#3f3f3f] text-lg text-right">
            12
        </p>
        <ul className="relative inter-regular text-[#757575] text-[13px] text-right">
          <a href="https://github.com/sisihae"></a>
          &nbsp;&nbsp;<a href="https://github.com/jj1kim"></a>
          &nbsp;&nbsp;<a href="https://github.com/jsh6269"></a>
          &nbsp;&nbsp;<a href="https://github.com/yunheeec"></a>
        </ul>
      </span>
    </footer>
  );
};

export default Footer;
